import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../../../../auth/_services/product.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
// import { SideBarService } from '../../../../layouts/side-bar/side-bar.service';
// import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../../_services/data.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
@Component({
    selector: 'app-add-color-list',
    templateUrl: "add-color.component.html",
    styles: []
})
export class AddColorComponent implements OnInit {


    form: FormGroup;
    private formSumitAttempt: boolean;
    constructor(private toastr: ToastrService, private route: ActivatedRoute, private router: Router, private formBuilder: FormBuilder, private _dataService: DataService, private _productService: ProductService) {


    }
    public userData;
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public attributeData = '';
    public selectedStatus = '';
    public product_id: any;
    public currentUser: any;
    p: number = 1;
    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.product_color != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        this.form = this.formBuilder.group({
            color_name: [null, Validators.required],
            color_code: [null, Validators.required],
            status: [null, Validators.required]
        });


        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));

    }


    isFieldValid(field: string) {
        return (
            //console.log('this.form.get',this.form.get(field));
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }

    onSubmit() {

        this.formSumitAttempt = true;
        this.route.params.subscribe(
            params => {
                if (params.id) {
                    this.product_id = params.id;

                    if (this.form.valid) {
                        this._productService.addColor({ form_data: this.form.value, fk_user_id: this.currentUser.success.id, fk_product_id: parseInt(this.product_id) }).subscribe(
                            data => {
                                //this.attributeData = data.success;
                                console.log(data);
                                this.toastr.success('Product Color Added Successfully', 'Success Message');
                                this.attributeData = data.data;
                                // that.form.c;
                                this.reset();
                            }
                        );
                    }
                }
            }
        );

    }

    reset() {
        if (this.selectedStatus != '') {
            this.form.reset();
            this.selectedStatus = '';
            this.formSumitAttempt = false;
        }
    }




    public search() {

        this.formSumitAttempt = true;

    }
    public vendor_delete(del_id) {


    }
    public changeStatus(user: any) {

    }

}
